

.product__header {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    margin-top: 15px;
    color: #D41443;
}

.product__label {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #FFA5BA;
    margin-top: 60px;
}

.product__name {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    padding-top: 160px;
    color: #FFFFFF;
    position: relative;
}
@media screen and (min-width:1200px) {
    .product__name {
    font-size: 30px;
    line-height: 34px;
    }
}

.product__name:after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #FFFFFF;
    box-shadow: -20px 0 0 #ffffff, 20px 0 0 #ffffff;
    transform: translateY(45px);
    left: 135px;
}
@media screen and (min-width:768px){
.product__name:after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #FFFFFF;
    box-shadow: -20px 0 0 #ffffff, 20px 0 0 #ffffff;
    transform: translateY(45px);
    left: 110px;
}}

@media screen and (min-width:1200px) {
    .product__name:after {
        content: "";
        position: absolute;
        width: 10px;
        height: 10px;
        border-radius: 50%;
