//-------------------------------------------------------------------
.more {
    display: none;
}
.more.is-open {
    display: block;
}
.dots.is-open {
    display: none;
}
//------------------------------------------------------------------
.header {
    background-color: #FFB8CA;
    position: relative;
    height: 502px;
    overflow: hidden;
    @media screen and (min-width: 768px) {
        height: 416px;
    }
    @media screen and (min-width: 1200px) {
        height: 664px;
    }
    &__title {
        position: relative;
        z-index: 1;
        width: 218px;
        margin-bottom: 20px;
        font-family: 'Titan One';
        font-size: 26px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #FFFFFF;
        &--span {
            font-size: 48px;
            line-height: 0.95;
            text-transform: uppercase;
            color: #D41443;
            @media screen and (min-width: 768px) {
                font-size: 34px;
                line-height: 0.90;
            }
            @media screen and (min-width: 1200px) {
                font-size: 58px;
                line-height: 0.90;
            }
        }
        @media screen and (min-width: 768px) {
            font-size: 22px;
            line-height: 1.13;
            width: 162px;
        }
        @media screen and (min-width: 1200px) {
            font-size: 38px;
            line-height: 1.15;
            width: 277px;
            margin-bottom: 24px;

        }
    }
    &__discr {
        display: none;
        @media screen and (min-width: 768px) {
            display: block;
            position: relative;
            bottom: 157px;
            z-index: 1;
            padding-left: 135px;
            max-width: 270px;
            font-size: 8px;
            line-height: 1.5;
            color: #FFFFFF;
        }
        @media screen and (min-width: 1200px) {
            max-width: 391px;
            padding-left: 204px;
            font-size: 14px;
            line-height: 1.4;
        }
        &--btn {
            display: none;
            @media screen and (min-width: 768px) {
                position: relative;
                top: -157px;
                left: 135px;
                display: flex;
                align-items: center;
                justify-content: center;
        
                width: 20px;
                height: 20px;
                fill: #d41443;
                background-color: #fff;
                border: none;
                border-radius: 50%;
                &.is-open .svg-right {
                    display: none;
                }
                & .svg-left {
                    display: none;
                }
                &.is-open .svg-left {
                    display: block;
                }
            }
            @media screen and (min-width: 1200px) {
                top: -150px;
                left: 203px;
                width: 30px;
                height: 30px;
            }
        }

      

    }
    &__images {
        &--ellipse {
            position: absolute;
            top: 17%;
            left: calc(100% - 283px);
            z-index: 0;
            width: 346px;
            height: 344px;
            border-radius: 50%;
            background-color: rgba(255, 165, 186, 1);
            @media screen and (min-width: 768px) {
                top: 13%;
                left: 32%;
                width: 335px;
                height: 333px;
            }
            @media screen and (min-width: 1200px) {
                left: 40%;
                width: 534px;
                height: 534px;
            }
        }
        &--icecream {
            position: absolute;
            top: 10px;
            left: 20%;
            z-index: 0;
            transform: translateY(0%);
            animation-name: icecream;
            animation-duration: 1000ms;
            animation-iteration-count: 1;
            @media screen and (min-width: 768px) {
                top: 26px;
            }
        }
        @keyframes icecream {
            from {
                transform: translateY(100%);
            }
            to {
                transform: translateY(0%);
            }
        }
        &--milk {
            display: none;
            @media screen and (min-width: 768px) {
                display: inline-block;
                position: relative;
                bottom: 45px;
                left: 0;
                z-index: 0;
                animation: animate__zoomInDown;
                animation-duration: 2000ms;

            }
            @media screen and (min-width: 1200px) {
                display: inline-block;
                position: relative;
                top: 0;
                left: 0;
                z-index: 0;
            }
        }
        &--girl {
            display: none;
            @media screen and (min-width: 768px) {
                display: inline-block;
                position: relative;
                bottom: 33px;
                left: 357px;
                z-index: 0;
                transform: translateX(0%);
                animation-name: girl;
                animation-duration: 1000ms;
                //animation-iteration-count: 1;
                animation-delay: 100ms;
                @keyframes girl {
                    from {
                        transform: translateX(100%);
                    }
                    to {
                        transform: translateX(0%);
                    }
                }
            }
            @media screen and (min-width: 1200px) {
                bottom: -70px;
                left: 645px;
                display: inline-block;
                position: relative;
                top: 0;
                right: 0;
                z-index: 0;
            }
        }
    }
}
.box-btn {
    position: absolute;
    z-index: 2;
    @media screen and (min-width: 768px) {
        position: relative;
        z-index: 1;
        margin-bottom: 61px;
    }

    &__products {
        position: relative;
        z-index: 1;
        background: #D41443;
        padding-top: 7px;
        padding-bottom: 7px;
        padding-right: 14px;
        padding-left: 14px;
        margin-right: 8px;
        border: none;
        border-radius: 20px;

        font-size: 8px;
        line-height: 10px;
        color: #ffffff;
        &:hover,
        &:focus {
            background: #ECEBEB;
            color: #D41443;
        }

        @media screen and (min-width: 1200px) {
            font-size: 14px;
            line-height: 1.28;
            padding-right: 23px;
            padding-left: 23px;
            padding-top: 11px;
            padding-bottom: 11px;
            margin-right: 12px;
        }
    }
    &__howmade {
       
        background-color: #FFFFFF;
        color: #D41443;
        padding-top: 7px;
        padding-bottom: 7px;
        padding-right: 14px;
        padding-left: 14px;
        border: none;
        border-radius: 20px;

        font-size: 8px;
        line-height: 10px;
        &:hover,
        &:focus {
            background: #ECEBEB;
            color: #D41443;
        }
        @media screen and (min-width: 1200px) {
            font-size: 14px;
            line-height: 1.28;
            padding-right: 23px;
            padding-left: 23px;
            padding-top: 11px;
            padding-bottom: 11px;
        }
    }
}

.box-counter {
    position: relative;
    z-index: 1;
    display: none;
    @media screen and (min-width: 768px) {
        display: block;
    }
    &__text {
        font-family: 'Titan One';
        font-size: 28px;
        line-height: 1.14;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #D41443;
        &--items {
            font-size: 8px;
            line-height: 1.25;
            color: #FFFFFF;
            @media screen and (min-width: 1200px) {
                font-size: 14px;
                line-height: 1.28;
            }
        }
        @media screen and (min-width: 1200px) {
            font-size: 46px;
            line-height: 1.15;
        }
    }
}

