.container-counter {
    display: flex;
    justify-content: space-between;
}
/*-----------------------------------------*/

.header-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 136px;
    @media screen and (min-width: 768px) {
        margin-bottom: 20px;
        padding-top: 32px;
        justify-content: stretch;
    }

    &__link {
        padding-top: 8px;
        @media screen and (min-width: 768px) {
            margin-right: 293px;
        }

        @media screen and (min-width: 1200px) {
            margin-right: 128px;
            padding-top: 0;
        }
    }
  }
  
  .menu-btn {
    z-index: 10;
    display: inherit;
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: none;
    &:hover,
    &:focus {
      fill: lightcoral;
    }
    @media screen and (min-width: 768px) {
        margin-right: 32px;
    }

    @media screen and (min-width: 1200px) {
        display: none;
    }
  }
  
  .menu-btn .menu-btn__cross {
    display: none;
  }
  
  .menu-btn.is-open .menu-btn__cross {
    display: block;
  }
  
  .menu-btn.is-open .menu-btn__burger {
    display: none;
  }

//Меню- контейнер
  .menu-container {
    @media screen and (max-width: 1199px) {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 3;
        background: #ffffff;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 2px 4px rgba(0, 0, 0, 0.08),
          0px 1px 3px rgba(0, 0, 0, 0.16);
        min-width: 250px;
        display: none;
        text-align: center;
    
        @keyframes menu {
            from {
                transform: translateX(100%);
            }
            to {
                transform: translateX(0%);
            }
        }
    }

    @media screen and (min-width: 768px) and (max-width: 1199px) {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    background: #ffffff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 2px 4px rgba(0, 0, 0, 0.08),
      0px 1px 3px rgba(0, 0, 0, 0.16);
    min-width: 250px;
    display: none;
    text-align: center;

    @keyframes menu {
        from {
            transform: translateX(100%);
        }
        to {
            transform: translateX(0%);
        }
    }
    @media screen and (min-width: 768px) {
        left: 0;
        width: 250px;
        @keyframes menu-tablet {
            from {
                transform: translateX(-100%);
            }
            to {
                transform: translateX(0%);
            }
        }
    }

    @media screen and (min-width: 1200px) {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }

  }
  .menu-container.is-open {
    display: block;
    padding-top: 60px;
    padding-bottom: 131px;
    background: #E17992;

    transform: translateX(0%);
    animation: menu;
    animation-duration: 500ms;
    @media screen and (min-width: 768px) {
        transform: translateX(0%);
        animation: menu-tablet;
        animation-duration: 500ms;
    }
  }
  .menu-container__btn {
    border: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    line-height: 21px;

    margin-bottom: 32px;
    padding-left: 48px;
    padding-right: 48px;
    height: 44px;

    color: #D41443;
    background: #FFFFFF;
    border-radius: 22px;
    @media screen and (min-width: 1200px) {
        margin-bottom: 0;
        padding-left: 37px;
        padding-right: 37px;
    }

    &--tablet {
        display: none;
        @media screen and (min-width: 768px) {
            display: block;
            border: none;
            font-weight: 700;
            font-size: 16px;
            line-height: 21px;
    
            padding-left: 37px;
            padding-right: 37px;
            height: 44px;
    
            color: #D41443;
            background: #FFFFFF;
            border-radius: 22px;
        }
        @media screen and (min-width: 1200px) {
            display: none;
        }
    }
  }

  }
  .menu {
    text-align: start;
    &__item {
        @media screen and (max-width: 1199px) {
            border-top: 1px solid rgba(225, 130, 152, 1);
            border-bottom: 1px solid rgba(225, 130, 152, 1);
            padding-top: 13px;
            padding-bottom: 13px;
        }
        @media screen and (min-width: 1200px) {
            margin-right: 35px;
            &:last-child {
                margin-right: 0;
            }
        }
        border-top: 1px solid rgba(225, 130, 152, 1);
        border-bottom: 1px solid rgba(225, 130, 152, 1);
        padding-top: 13px;
        padding-bottom: 13px;
    }
    @media screen and (min-width: 1200px) {
        display: flex;
        margin-right: 141px;
    }
    &__text {
        font-weight: 700;
        font-size: 14px;
        line-height: 1.28;
        color: #FFFFFF;
        padding-left: 40px;
        padding-right: 40px;
        &:hover,
        &:focus {
            color: rgba(212, 20, 67, 1);
        }

        @media screen and (min-width: 1200px) {
            padding-left: 0;
            padding-right: 0;
            font-size: 16px;
            line-height: 1.31;
        }
    }
    &__pointer {
        margin-left: 16px;
        @media screen and (min-width: 768px) {
            margin-left: 11px;
        }
    }
  }

  /*------------------------------------------------------------*/

  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    z-index: 20;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

}
//------------------------------------------------
.backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}
//-------------------------------------------------
.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    margin: 0;
    padding: 0;
  
    background-color: #ffffff;
    width: calc(100% - 40px);
    background: #ffffff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    &__btn {
        position: absolute;
        top: 0;
        left: calc(100% - 40px);
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        background: #ffffff;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        margin-top: 10px;
        cursor: pointer;
    }

    &__icon {
        position: absolute;
        top: 58%;
        left: 50%;
        width: 18px;
        height: 18px;
        transform: translate(-50%, -50%);
        transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
        &:hover,
        &:focus {
            fill: lightcoral;
        }
    }
}
//------------------------------------------------
.modal-form {
    font-weight: 700;
    font-size: 20px;
    line-height: 1.5;
    letter-spacing: 0.03em;
    color: #000000;
    padding: 40px;
    &__title {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        letter-spacing: 0.03em;
        text-align: center;
        color: #000000;
        margin-bottom: 12px;
    }
}
//-----------------------------------------------
.form-group {
    margin-bottom: 20px;
    display: block;
    line-height: 1.16;
    &__field {
        display: block;
        margin-bottom: 10px;
        line-height: 1.16;
    }
    &__field:last-child {
        display: block;
        margin-bottom: 0;
    }
    &__name {
        display: block;
        font-weight: 400;
        font-size: 12px;
        line-height: 1.16;
        letter-spacing: 0.01em;
        color: #000000;
        margin-bottom: 4px;
    }
    &__icon-box {
        position: relative;
        fill: #000000;
    }
    &__icon {
        position: absolute;
        top: 65%;
        left: 12px;
        width: 18px;
        height: 18px;
        fill: #000000;
        transform: translateY(-50%);
        transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    &__input {
        display: inline-block;
        border: 1px solid rgba(33, 33, 33, 0.2);
        border-radius: 4px;
        width: 100%;
        height: 40px;
        padding-left: 40px;
    
        font-weight: 400;
        font-size: 12px;
        line-height: 1.16;
        letter-spacing: 0.01em;
        color: #000000;
        transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1), outline 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    &__input:focus ~ &__icon{
        fill: lightcoral;
    }
    &__input:focus {
        border-color: lightcoral;
        outline: lightcoral;
    }
    &__textarea {
        display: block;
        border: 1px solid rgba(33, 33, 33, 0.2);
        border-radius: 4px;
        width: 100%;
        height: 120px;
        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 16px;
        padding-right: 16px;
    
        width: 100%;
        resize: none;
    
        font-weight: 400;
        font-size: 12px;
        line-height: 1.16;
        letter-spacing: 0.01em;
        color: #000000;
        transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1), outline 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    &__textarea:focus {
        border-color: lightcoral;
        outline: lightcoral;
    }
}
.agreement__btn {
    display: block;
    margin: 0 auto;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 55px;
    padding-right: 55px;
    cursor: pointer;   
    background: lightcoral;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border: none;
    border-radius: 4px;  
    font-weight: 700;
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.06em;
    color: #ffffff;
}
