.backdrop {
  width: 100%;
  height: 100%;
  z-index: 20;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  width: calc(100% - 40px);
  background: #fff;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal__btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  display: flex;
  position: absolute;
  top: 0;
  left: calc(100% - 40px);
}

.modal__icon {
  width: 18px;
  height: 18px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__icon:hover, .modal__icon:focus {
  fill: #f08080;
}

.modal-form {
